.header {
    background-color: #0173C7;
    padding: 20px;
    color:#eee;
    .header-icon {
        background-color: #f9f9f9;
        padding: 10px;
        border-radius: 50%;
        color: #555;
        cursor: pointer;
        margin-left: 10px;
      }
    .toggle-sidebar {
      cursor: pointer;
    }
    .notifications-container {
      display: inline-block;
      margin-left: 20px;
      .dropdown-menu {
        min-width: 600px;
        border: none;
        padding: 0;
        top: 10px !important;
        &:before {
          content: "";
          position: absolute;
          top: -20px;
          left: 11px;
          border-style: solid;
          border-width: 10px;
          border-color: transparent transparent #333 transparent;
        }
        .noti-head {
          background-color: #333;
          padding: 15px;
          color: #fff;
          span {
            float: right;
          }
        }
        .noti-content {
          padding: 20px;
          ul {
            margin: 0;
            li {
              padding: 10px;
              i {
                background-color: #eee;
                padding: 10px;
                border-radius: 50%;
                width: 45px;
                height: 45px;
                line-height: 26px;
                text-align: center;
                margin-right: 10px;
                position: relative;
                top: -2px;
              }
            }
          }
        }
        .noti-foot {
          a {
            display: block;
            padding: 10px;
            border-top: 1px solid #eee;
            &:hover {
              text-decoration: none;
            }
          }
        }
      }
    }
  }